<template>
  <template v-if="editable">
    {{ dateFormatte }}
    <selecteur-date
      :originale="valValide"
      @modifiee="({val}) => (valFinale = val)"
    >
      <template #activateur="{props: propsActivateur}">
        <v-icon
          v-bind="propsActivateur"
          icon="mdi-calendar-clock"
        />
      </template>
    </selecteur-date>
  </template>
  <template v-else>
    <span>{{ dateFormatte }}</span>
  </template>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';
import {computed, ref, watch} from 'vue';

import SelecteurDate from './SlecteurDate.vue';

const props = defineProps<{val?: types.lmentsBd; editable: boolean}>();
const mettre = defineEmits<{(: 'modifiee', args: {val?: number}): void}>();

const valValide = computed(() => {
  if (typeof props.val === 'number') return props.val;
  else return undefined;
});

const dateFormatte = computed(() => {
  if (!valValide.value) return undefined;
  return new Date(valValide.value).toLocaleDateString();
});

// Modifications
const valFinale = ref(valValide.value);
watch(valFinale, () => {
  mettre('modifiee', {val: valFinale.value || undefined});
});
</script>
