<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activateur"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      class="mx-auto"
      :min-width="mdAndUp ? 500 : 300"
    >
      <v-card-item>
        <v-card-title class="d-flex align-center">
          <nom-tableau :id-tableau="idTableau" />
          <DialogueNoms
            :etiquette-nom="t('objet.tiquetteNom')"
            :indice-nom="t('objet.indiceNom')"
            :indice-langue="t('objet.indiceLangue')"
            :texte-aucun-nom="t('objet.texteAucunNom')"
            :noms-initiaux="noms"
            :titre="t('objet.titreDialogueNoms')"
            :sous-titre="t('objet.sousTitreDialogueNoms')"
            :autorisation-modification="!!monAutorisation"
            @ajuster-noms="nms => changerNoms(nms)"
          >
            <template #activateur="{props: propsActivateur}">
              <v-btn
                v-bind="propsActivateur"
                class="ms-2"
                :icon="monAutorisation ? 'mdi-pencil' : 'mdi-earth'"
                variant="flat"
                size="small"
              />
            </template>
          </DialogueNoms>
          <lien-objet :id="idTableau" />
          <v-spacer />
          <v-btn
            icon="mdi-close"
            size="small"
            variant="flat"
            @click="dialogue = false"
          />
        </v-card-title>
      </v-card-item>
      <v-card-text>
        <division-carte :titre="t('tableaux.carte.optionsAvances')" />
        <v-list-item
          variant="flat"
          :append-icon="clefCopie ? 'mdi-check' : 'mdi-content-copy'"
          @click="() => copierClef()"
        >
          {{ t('tableaux.carte.clef') }}
          {{ clefTableau }}
        </v-list-item>
        <carte-effacer
          v-if="monAutorisation"
          @effacer="() => mettre('effacer')"
        >
          <template #activateur="{props: propsActivateur}">
            <v-list-item
              v-bind="propsActivateur"
              class="text-error"
              variant="flat"
              append-icon="mdi-delete"
            >
              {{ t('communs.effacer') }}
            </v-list-item>
          </template>
        </carte-effacer>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {_} from '@lassi-js/kilimukku-vue';
import {computed, ref} from 'vue';
import {useDisplay} from 'vuetify';
import DivisionCarte from '../communs/DivisionCarte.vue';
import {utiliserConstellation} from '../utils';
import CarteEffacer from '/@/components/communs/CarteEffacer.vue';
import LienObjet from '/@/components/communs/LienObjet.vue';
import DialogueNoms from '/@/components/communs/listeNoms/DialogueNoms.vue';
import NomTableau from '/@/components/tableaux/NomTableau.vue';
import {ajusterTexteTraductible, copier} from '/@/utils';

const props = defineProps<{idTableau: string; clefTableau: string}>();
const mettre = defineEmits<{
  (: 'effacer'): void;
}>();

const {$: t} = _();
const {mdAndUp} = useDisplay();

const constl = utiliserConstellation();

// Navigation
const dialogue = ref(false);

// Noms
const noms = suivre(constl.tableaux.suivreNomsTableau, {
  idTableau: computed(() => props.idTableau),
}); //  faire - voir comment utiliser constl.nues.suivreNomsTableauNue

const changerNoms = async (nms: {[langue: string]: string}) => {
  const {Effacer, Ajouter} = ajusterTexteTraductible({anciennes: noms.value, nouvelles: nms});
  for (const langue of Effacer) {
    await constl.tableaux.effacerNomTableau({idTableau: props.idTableau, langue});
  }
  await constl.tableaux.sauvegarderNomsTableau({idTableau: props.idTableau, noms: Ajouter});
};

// Autorisation
const monAutorisation = suivre(constl.suivrePermission, {idObjet: computed(() => props.idTableau)});

// Clef tableau
const clefCopie = ref(false);
const copierClef = async () => {
  await copier(props.clefTableau);
  clefCopie.value = true;
};
</script>
