<template>
  <v-list-item>
    <template #prepend>
      <image-profil :id="idDUnAuteur"> </image-profil>
    </template>
    <v-list-item-title>
      {{ t('auteurs.item.titre', nAuteursAccepts) }}
    </v-list-item-title>
  </v-list-item>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';

import {computed} from 'vue';

import {_} from '@lassi-js/kilimukku-vue';
import ImageProfil from './ImageProfil.vue';

const {$: t} = _();

const props = defineProps<{auteurs: types.infoAuteur[] | undefined}>();

// Auteurs
const auteursAccepts = computed(() => {
  return (props.auteurs || []).filter(a => a.accept);
});
const idDUnAuteur = computed(() => {
  if (auteursAccepts.value.length) {
    return auteursAccepts.value[Math.floor(auteursAccepts.value.length * Math.random())]
      ?.idCompte;
  }
  return undefined;
});

const nAuteursAccepts = computed(() => {
  return auteursAccepts.value.length;
});
</script>
