import type {variables as typesVariables, valid} from '@constl/ipa';

import {utiliserConstellation} from '../utils';

export const crerColonneTableau = async ({
  idTableau,
  idVariable,
  idColonne,
  index,
  rgles,
}: {
  idTableau: string;
  idVariable: string;
  idColonne?: string;
  index?: boolean;
  rgles: valid.rgleVariable[];
}) => {
  const constl = utiliserConstellation();
  const idColonneFinale = await constl.tableaux.ajouterColonneTableau({
    idTableau,
    idVariable: idVariable,
    idColonne,
  });
  if (!idColonneFinale) return;

  if (index) {
    await constl.tableaux.changerColIndex({
      idTableau,
      idColonne: idColonneFinale,
      val: true,
    });
  }
  for (const rgle of rgles) {
    await constl.tableaux.ajouterRgleTableau({
      idTableau,
      idColonne: idColonneFinale,
      rgle: rgle,
    });
  }
};

const triables: typesVariables.catgorieBaseVariables[] = [
  'boolen',
  'chane',
  'chaneNonTraductible',
  'horoDatage',
  'intervaleTemps',
  'numrique',
];
export const triable = (catgorieBase: typesVariables.catgorieBaseVariables): boolean => {
  return triables.includes(catgorieBase);
};
