<template>
  <cellule-horo-datage
    v-if="catgorieFinale === 'horoDatage'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-chaine-non-traductible
    v-else-if="catgorieFinale === 'chaneNonTraductible'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-booleenne
    v-else-if="catgorieFinale === 'boolen'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-numerique
    v-else-if="catgorieFinale === 'numrique'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-audio
    v-else-if="catgorieFinale === 'audio'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-image
    v-else-if="catgorieFinale === 'image'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-video
    v-else-if="catgorieFinale === 'vido'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-fichier
    v-else-if="catgorieFinale === 'fichier'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-geo-json
    v-else-if="catgorieFinale === 'gojson'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-intervale-temps
    v-else-if="catgorieFinale === 'intervaleTemps'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <cellule-chaine
    v-else-if="catgorieFinale === 'chane'"
    :val="val"
    :editable="editable"
    @modifiee="({val}) => modifie({val})"
  />
  <span v-else>{{ val }}</span>
</template>
<script setup lang="ts">
import type {types, variables} from '@constl/ipa';
import {computed} from 'vue';

import {devinerCatgorie} from '@constl/utils-ipa';

import CelluleAudio from './CelluleAudio.vue';
import CelluleBooleenne from './CelluleBoolenne.vue';
import CelluleChaine from './CelluleChane.vue';
import CelluleChaineNonTraductible from './CelluleChaneNonTraductible.vue';
import CelluleFichier from './CelluleFichier.vue';
import CelluleGeoJson from './CelluleGoJson.vue';
import CelluleHoroDatage from './CelluleHoroDatage.vue';
import CelluleImage from './CelluleImage.vue';
import CelluleIntervaleTemps from './CelluleIntervaleTemps.vue';
import CelluleNumerique from './CelluleNumrique.vue';
import CelluleVideo from './CelluleVido.vue';

const props = defineProps<{
  categorie?: variables.catgorieBaseVariables;
  val: types.lmentsBd;
  editable: boolean;
}>();
const mettre = defineEmits<{
  (: 'modifiee', args: {val?: types.lmentsBd | File}): void;
}>();

const catgorieFinale = computed<variables.catgorieBaseVariables | undefined>(() => {
  if (props.categorie) return props.categorie;
  else return devinerCatgorie(props.val);
});

// Modifications
const modifie = ({val}: {val?: types.lmentsBd | File}) => {
  mettre('modifiee', {val});
};
</script>
