<template>
  <template v-if="editable"> </template>
  <template v-else>
    <TexteTronqu
      v-if="texteTraduit"
      :texte="texteTraduit"
      :longueur-max="30"
    />
    <v-skeleton-loader
      v-else-if="valValide"
      type="text"
    />
  </template>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';
import {suivre} from '@constl/vue';
import {type ComputedRef, computed} from 'vue';

import {adresseOrbiteValide} from '@constl/utils-ipa';
import {_} from '@lassi-js/kilimukku-vue';
import {utiliserConstellation} from '/@/components/utils';

import TexteTronqu from '/@/components/communs/TexteTronqu.vue';
// import DialogueNoms from '/@/components/communs/listeNoms/DialogueNoms.vue';

const props = defineProps<{val?: types.lmentsBd; editable: boolean}>();

const constl = utiliserConstellation();

const {_} = _();

const valValide = computed(() => {
  if (typeof props.val === 'string' && adresseOrbiteValide(props.val)) return props.val;
  else return undefined;
});

// Texte traduit
const textes: ComputedRef<{[langue: string]: string}> = suivre(constl.suivreBdDic, {id: valValide});
const texteTraduit = _(textes);
</script>
