<template>
  <v-list-item>
    <template #prepend>
      <v-icon
        start
        :color="couleur"
      >
        {{ icne }}
      </v-icon>
    </template>
    <template #title>
      {{ t(`donnes.statut.${statut.statut}`) }}
      <v-btn
        v-if="statut.idNouvelle"
        icon="mdi-open-in-new"
        @click="
          () =>
            $router.push(
              encodeURI(`/donnes/${typeObjet}/${encodeURIComponent(statut.idNouvelle as string)}`),
            )
        "
      >
        {{ t('statut.allerNouvelle') }}
      </v-btn>
    </template>
  </v-list-item>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';

import {_} from '@lassi-js/kilimukku-vue';
import {computed} from 'vue';
import {icneStatut} from '../utils';

const {$: t} = _();

const props = defineProps<{
  typeObjet: 'bd' | 'nue' | 'projet';
  statut: types.schmaStatut;
}>();

const couleur = computed(() => {
  switch (props.statut.statut) {
    case 'active': {
      return 'primary';
    }
    case 'jouet':
    case 'interne':
    case 'obsolte': {
      return 'warn';
    }
    default:
      return undefined;
  }
});

const icne = computed(() => {
  return icneStatut(props.statut.statut);
});
</script>
