<template>
  <v-list-item>
    <template #prepend>
      <v-icon
        left
        icon="mdi-database-sync"
      />
    </template>
    <v-list-item-title>
      <v-skeleton-loader
        v-if="rplications === undefined"
        type="chip"
      />
      <span v-else>
        {{ t('rplications.rplications', {n: nRplications}) }}
      </span>
    </v-list-item-title>
  </v-list-item>
</template>
<script setup lang="ts">
import {rechercher} from '@constl/vue';

import {_} from '@lassi-js/kilimukku-vue';
import {computed} from 'vue';
import {utiliserConstellation} from '/@/components/utils';

const props = defineProps<{id: string}>();

const {$: t} = _();

const constl = utiliserConstellation();

const {rsultats: rplications} = rechercher(constl.rseau.suivreRplications, {
  idObjet: computed(() => props.id),
  profondeur: 10,
});

const nRplications = computed(() => rplications.value?.dispositifs.length || 0);
</script>
