<template>
  <v-text-field
    v-if="editable"
    v-model="valFinale"
    hide-details
    density="compact"
    variant="outlined"
  />
  <p v-else>{{ valFormatte }}</p>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';

import {computed, ref, watchEffect} from 'vue';

import {_, _} from '@lassi-js/kilimukku-vue';
import {watch} from 'vue';

const props = defineProps<{val: types.lmentsBd; editable: boolean}>();
const mettre = defineEmits<{
  (: 'modifiee', args: {val?: number}): void;
}>();

const  = _();
const {_} = _();

const valValide = computed(() => (typeof props.val === 'number' ? props.val : undefined));
const valFormatte = _(valValide);
const valFinale = ref('');
watchEffect(() => {
  valFinale.value = valFormatte.value || '';
});

const valFinaleNumrique = computed(() => {
  try {
    return .({: valFinale.value});
  } catch {
    return undefined;
  }
});

watch(valFinaleNumrique, () => {
  mettre('modifiee', {val: valFinaleNumrique.value});
});
</script>
