<template>
  <template v-if="editable">
    <input
      ref="HTMLChoixFichier"
      type="file"
      style="display: none"
      :accept="accepter"
      @change="lorsqueFichierChoisi"
    />
    <v-chip
      v-if="slectionFichier"
      label
      closable
      variant="outlined"
      :prepend-icon="icone || 'mdi-file-outline'"
      @click:close="() => effacerSlection()"
      @click="() => ouvrirFentreChoisirFichier()"
    >
      <TexteTronque
        :texte="slectionFichier.name"
        :longueur-max="20"
      />
    </v-chip>
    <v-chip
      v-else-if="valValide && slectionFichier !== null"
      label
      size="small"
      variant="outlined"
      :prepend-icon="icone || 'mdi-file-outline'"
    >
      <TexteTronque
        :texte="valValide.fichier"
        :longueur-max="20"
      />
      <v-icon
        icon="mdi-close"
        end
        @click="() => effacerSlection()"
      />
    </v-chip>
    <v-btn
      v-else
      icon="mdi-upload"
      size="small"
      variant="flat"
      @click="() => ouvrirFentreChoisirFichier()"
    />
  </template>
  <template v-else-if="valValide">
    <v-dialog v-model="dialogue">
      <template #activator="{props: propsActivateur}">
        <v-chip
          v-bind="propsActivateur"
          label
          size="small"
          variant="outlined"
          :prepend-icon="icone || 'mdi-file-outline'"
        >
          <TexteTronque
            :texte="valValide.fichier"
            :longueur-max="20"
          />
          <v-icon
            icon="mdi-download"
            end
            @click.stop="() => tlcharger()"
          />
        </v-chip>
      </template>
      <slot
        name="visualisation"
        :val-actuelle="valValide"
        :tlcharger="tlcharger"
        :fermer="() => (dialogue = false)"
      >
        <v-card class="mx-auto">
          <v-card-item>
            <v-card-title>{{ valValide.fichier }}</v-card-title>
          </v-card-item>
          <v-card-actions>
            <v-spacer />
            <v-btn
              :text="t('communs.tlcharger')"
              variant="outlined"
              append-icon="mdi-download"
              @click="() => tlcharger()"
            />
            <v-btn
              :text="t('communs.fermer')"
              variant="outlined"
              append-icon="mdi-close"
              @click="() => (dialogue = false)"
            />
          </v-card-actions>
        </v-card>
      </slot>
    </v-dialog>
  </template>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';

import {computed, ref, watch} from 'vue';

import {idcEtExt} from '@constl/utils-ipa';
import {_} from '@lassi-js/kilimukku-vue';
import TexteTronque from '/@/components/communs/TexteTronqu.vue';
import {utiliserConstellation} from '/@/components/utils';
import {itrableFlux, tlchargerFlux} from '/@/utils';

const props = defineProps<{
  val?: types.lmentsBd;
  editable: boolean;
  icone?: string;
  accepter?: string;
}>();
const mettre = defineEmits<{(: 'modifiee', args: {val?: File}): void}>();

const constl = utiliserConstellation();

const {$: t} = _();

const valValide = computed(() => {
  if (typeof props.val === 'string') {
    return idcEtExt(props.val);
  }
  return undefined;
});

// Choisir fichier
const slectionFichier = ref<File | null>();

const HTMLChoixFichier = ref<HTMLInputElement>();
const ouvrirFentreChoisirFichier = () => {
  HTMLChoixFichier.value?.click();
};
const lorsqueFichierChoisi = async (): Promise<void> => {
  if (!HTMLChoixFichier.value?.files?.length) return;
  slectionFichier.value = HTMLChoixFichier.value.files[0];
};

const effacerSlection = () => (slectionFichier.value = null);

watch(slectionFichier, () => {
  mettre('modifiee', {val: slectionFichier.value || undefined});
});

// Tlcharger fichier
const tlcharger = async () => {
  // Tlcharger le document nouvellement ajout si disponible ; sinon, tlcharger la valeur originale.
  if (slectionFichier.value) {
    tlchargerFlux({
      flux: new Blob([slectionFichier.value]).stream(),
      nom: slectionFichier.value.name,
    });
  } else if (valValide.value) {
    const itrable = await constl.obtItrableAsyncSFIP({id: valValide.value.id});
    const flux = itrableFlux(itrable);
    tlchargerFlux({flux, nom: valValide.value.fichier});
  }
};

// Visualisation
const dialogue = ref(false);
</script>
