<template>
  <cellule-fichier
    :editable="editable"
    :val="val"
    icone="mdi-waveform"
    accepter="audio/*"
    @modifiee="({val}) => mettre('modifiee', {val})"
  >
    <template #visualisation="{fermer, tlcharger}">
      <v-card class="mx-auto">
        <v-card-item>
          <v-card-title>{{ valValide?.fichier }}</v-card-title>
        </v-card-item>
        <v-card-text>
          <video-player
            :src="{type: 'audio/mp3', src: srcVido}"
            controls
            audio-only-mode
            :width="500"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            :text="t('communs.tlcharger')"
            variant="outlined"
            append-icon="mdi-download"
            @click="() => tlcharger()"
          />
          <v-btn
            :text="t('communs.fermer')"
            variant="outlined"
            append-icon="mdi-close"
            @click="() => fermer()"
          />
        </v-card-actions>
      </v-card>
    </template>
  </cellule-fichier>
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';
import {idcEtExt} from '@constl/utils-ipa';

import {_} from '@lassi-js/kilimukku-vue';
import {computed, ref, watchEffect} from 'vue';
import CelluleFichier from './CelluleFichier.vue';
import {utiliserConstellation} from '/@/components/utils';

import 'video.js/dist/video-js.css';
import {defineAsyncComponent} from 'vue';

// On importe celui-ci dynamiquement parce qu'il est trs lourd
const VideoPlayer = defineAsyncComponent(() => import('@videojs-player/vue'));

const props = defineProps<{val?: types.lmentsBd; editable: boolean}>();
const mettre = defineEmits<{(: 'modifiee', args: {val?: File}): void}>();

const constl = utiliserConstellation();

const {$: t} = _();

//  faire - obtenir l'objet valid directement de CelluleFichier.vue
const valValide = computed(() => {
  if (typeof props.val === 'string') {
    return idcEtExt(props.val);
  }
  return undefined;
});

// Vido
const srcVido = ref<string>();
watchEffect(async () => {
  if (!valValide.value) return undefined;
  const fichier = await constl.obtFichierSFIP({id: valValide.value?.id});
  if (fichier) {
    srcVido.value = URL.createObjectURL(
      new Blob([fichier], {type: `video/${valValide.value.ext}`}),
    );
  } else {
    srcVido.value = undefined;
  }
});
</script>
