<template>
  <TexteTronque
    :texte="nomTraduit || clef"
    :longueur-max="15"
  />
  <carte-tableau
    :id-tableau="id"
    :clef-tableau="clef"
    @effacer="() => mettre('effacer')"
  >
    <template #activateur="{props: propsActivateur}">
      <v-icon
        v-bind="propsActivateur"
        class="ms-2"
        icon="mdi-dots-horizontal"
        variant="flat"
        size="small"
      />
    </template>
  </carte-tableau>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {_} from '@lassi-js/kilimukku-vue';
import {utiliserConstellation} from '../utils';
import CarteTableau from './CarteTableau.vue';

import {computed} from 'vue';
import TexteTronque from '/@/components/communs/TexteTronqu.vue';

const props = defineProps<{id: string; clef: string}>();
const mettre = defineEmits<{
  (: 'effacer'): void;
}>();

const {_} = _();

const constl = utiliserConstellation();

// Noms
const noms = suivre(constl.tableaux.suivreNomsTableau, {idTableau: computed(() => props.id)}); //  faire - voir comment utiliser constl.nues.suivreNomsTableauNue
const nomTraduit = _(noms);
</script>
