<template>
  <v-checkbox
    v-if="editable"
    v-model="valFinale"
    :readonly="!editable"
    density="compact"
    hide-details
  />
  <v-icon
    v-else-if="valFinale !== undefined"
    :icon="valFinale ? 'mdi-checkbox-marked' : 'mdi-checkbox-blank-outline'"
  />
</template>
<script setup lang="ts">
import type {types} from '@constl/ipa';
import {computed, ref, watch, watchEffect} from 'vue';

const props = defineProps<{val?: types.lmentsBd; editable: boolean}>();
const mettre = defineEmits<{(: 'modifiee', args: {val?: boolean}): void}>();

const valValide = computed(() => {
  return typeof props.val === 'boolean' ? props.val : undefined;
});
const valFinale = ref(valValide.value);
watchEffect(() => {
  valFinale.value = valValide.value;
});

watch(valFinale, () => {
  mettre('modifiee', {val: valFinale.value});
});
</script>
