<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activateur"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      class="mx-auto"
      :max-width="mdAndUp ? 500 : 300"
    >
      <v-card-item class="mb-0">
        <v-card-title class="d-flex align-center align-center">
          {{ t('colonnes.carte.titre') }}
          <lien-objet
            :id="idColonne"
            :editable="!!monAutorisation"
            @modifie="x => (choixIdColonne = x)"
          />
          <v-spacer />
          <v-btn
            icon="mdi-close"
            size="small"
            variant="flat"
            @click="dialogue = false"
          ></v-btn>
        </v-card-title>
      </v-card-item>
      <v-divider />
      <v-card-text style="overflow-y: auto">
        <division-carte
          :titre="t('colonnes.carte.variable')"
          :en-attente="!rglesAfficher"
        />
        <selecteur-variable
          v-if="permissionModifier"
          :multiples="false"
          :originales="idVariable ? [idVariable] : undefined"
          @selectionnee="ids => choisirVariable({idVar: ids[0]})"
        />
        <jeton-variable
          v-else-if="idVariable"
          :id="idVariable"
        />

        <division-carte
          :titre="t('colonnes.carte.rgles')"
          :en-attente="!rglesAfficher"
        />
        <v-list>
          <nouvelle-regle
            v-if="permissionModifier"
            :source="{
              type: 'tableau',
              idTableau,
              idColonne,
              idVariable: choixVariable,
            }"
            :categorie-variable="catgorieBaseVariable"
            :existantes="
              rglesAfficher.filter(r => r.source.type === 'tableau').map(r => r.rgle.rgle)
            "
            @sauvegarder="r => ajouterRgle(r)"
          >
            <template #activateur="{props: propsActivateurNouvelleRgle}">
              <v-list-item
                v-bind="propsActivateurNouvelleRgle"
                prepend-icon="mdi-plus"
              >
                <v-list-item-title>
                  {{ t('variables.rgles.nouvelle') }}
                </v-list-item-title>
              </v-list-item>
            </template>
          </nouvelle-regle>
          <item-regle-colonne
            v-for="r in rglesAfficher"
            :key="r.rgle.id"
            :regle="r"
            :autorisation-modifier="permissionModifier"
            @effacer="() => effacerRgle(r.rgle.id)"
          ></item-regle-colonne>
        </v-list>
        <section-retractible
          :titre="t('colonnes.carte.avances')"
          initial="ferm"
        >
          <v-checkbox
            v-model="choixIndex"
            :disabled="!permissionModifier"
            color="primary"
            hide-details
          >
            <template #label>
              {{ t('colonnes.nouvelle.index') }}
              <v-tooltip :text="t('tableaux.colonnes.indiceIndex')">
                <template #activator="{props: propsActivateur}">
                  <v-icon
                    v-bind="propsActivateur"
                    icon="mdi-information-outline"
                    end
                  />
                </template>
              </v-tooltip>
            </template>
          </v-checkbox>
          <v-dialog v-model="dialogueEffacer">
            <template #activator="{props: propsActivateur}">
              <v-list-item
                v-bind="propsActivateur"
                class="text-error"
                variant="flat"
                append-icon="mdi-delete"
              >
                {{ t('communs.effacer') }}
              </v-list-item>
            </template>
            <v-card
              class="mx-auto"
              max-width="300"
            >
              <v-card-item>
                <p class="text-h5">
                  {{ t('tableaux.colonnes.confirmerEffacer') }}
                </p>
              </v-card-item>
              <v-card-actions>
                <v-btn @click="dialogueEffacer = false">{{ t('communs.non') }}</v-btn>
                <v-btn
                  color="error"
                  @click="() => mettre('effacer')"
                >
                  {{ t('communs.oui') }}
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </section-retractible>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <btn-sauvegarder
          :actif="modifi"
          @click="sauvegarder"
        />
        <btn-annuler @click="fermer" />
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import type {valid} from '@constl/ipa';

import {suivre} from '@constl/vue';
import {v4 as uuidv4} from 'uuid';
import {computed, ref, watchEffect} from 'vue';
import {useDisplay} from 'vuetify';

import {_} from '@lassi-js/kilimukku-vue';
import BtnAnnuler from '/@/components/communs/BtnAnnuler.vue';
import BtnSauvegarder from '/@/components/communs/BtnSauvegarder.vue';
import DivisionCarte from '/@/components/communs/DivisionCarte.vue';
import LienObjet from '/@/components/communs/LienObjet.vue';
import SectionRetractible from '/@/components/communs/SectionRtractible.vue';
import ItemRegleColonne from '/@/components/rgles/ItemRgleColonne.vue';
import NouvelleRegle from '/@/components/rgles/NouvelleRgle.vue';
import JetonVariable from '/@/components/variables/JetonVariable.vue';
import SelecteurVariable from '/@/components/variables/SlecteurVariable.vue';

import {utiliserConstellation} from '../utils';

const props = defineProps<{
  permissionModifier: boolean;
  idVariable?: string;
  idColonne: string;
  idTableau: string;
  regles?: valid.rgleColonne[];
  index: boolean;
}>();
const mettre = defineEmits<{
  (
    : 'sauvegarder',
    args: {
      index: boolean;
      variable?: string;
      rgles: {
        nouvelles: valid.rgleVariable[];
        Effacer: string[];
      };
    },
  ): void;
  (: 'effacer'): void;
}>();

const {mdAndUp} = useDisplay();

const {$: t} = _();

const constl = utiliserConstellation();

// Navigation
const dialogue = ref(false);
const dialogueEffacer = ref(false);

// Autorisation
const monAutorisation = suivre(constl.suivrePermission, {idObjet: computed(() => props.idTableau)});

// Index
const choixIndex = ref(props.index);
const indexModifi = computed(() => choixIndex.value !== props.index);
watchEffect(() => (choixIndex.value = props.index));

// Id
const choixIdColonne = ref(props.idColonne);
const idColonneModifie = computed(() => choixIdColonne.value !== props.idColonne);
watchEffect(() => (choixIdColonne.value = props.idColonne));

watchEffect(async () => {
  if (idColonneModifie.value)
    await constl.tableaux.changerIdColonne({
      idTableau: props.idTableau,
      idColonne: props.idColonne,
      nouvelleIdColonne: choixIdColonne.value,
    });
});

// Variable
const choixVariable = ref(props.idVariable);
watchEffect(() => (choixVariable.value = props.idVariable));

const variableModifie = computed(() => choixVariable.value !== props.idVariable);
const catgorieVariable = suivre(constl.variables.suivreCatgorieVariable, {
  idVariable: choixVariable,
});
const catgorieBaseVariable = computed(() => {
  return catgorieVariable.value?.catgorie;
});
const choisirVariable = ({idVar}: {idVar: string}) => {
  choixVariable.value = idVar;
};

// Rgles
const nouvellesRgles = ref<valid.rgleVariableAvecId[]>([]);
const rglesEffacer = ref<string[]>([]);

const rglesAfficher = computed<valid.rgleColonne[]>(() => {
  const nouvelles: valid.rgleColonne[] = nouvellesRgles.value.map(r => ({
    rgle: r,
    source: {
      type: 'tableau',
      id: props.idTableau,
    },
    colonne: props.idColonne,
  }));
  return [...(props.regles || []), ...nouvelles].filter(
    r => !rglesEffacer.value.includes(r.rgle.id),
  );
});

const effacerRgle = (id: string) => {
  if (rglesEffacer.value.includes(id)) return;
  if (nouvellesRgles.value?.some(r => r.id === id)) {
    nouvellesRgles.value = nouvellesRgles.value?.filter(r => r.id !== id);
  } else if (props.regles?.some(r => r.rgle.id === id)) {
    rglesEffacer.value = [id, ...rglesEffacer.value];
  }
};
const ajouterRgle = (r: valid.rgleVariable) => {
  nouvellesRgles.value = [{rgle: r, id: uuidv4()}, ...(nouvellesRgles.value || [])];
};

const rglesModifies = computed(() => {
  return !!(nouvellesRgles.value.length || rglesEffacer.value.length);
});

// Sauvegarder
const modifi = computed(() => {
  return indexModifi.value || variableModifie.value || rglesModifies.value;
});
const sauvegarder = () => {
  mettre('sauvegarder', {
    index: choixIndex.value,
    variable: choixVariable.value,
    rgles: {
      nouvelles: nouvellesRgles.value.map(r => r.rgle),
      Effacer: rglesEffacer.value,
    },
  });
  fermer();
};
const fermer = () => {
  dialogue.value = false;
  nouvellesRgles.value = [];
  rglesEffacer.value = [];
};
</script>
